﻿import * as THREE from './../../node_modules/three/src/constants';
import {Mesh} from './../../node_modules/three/src/objects/Mesh';
import {Clock} from './../../node_modules/three/src/core/Clock';
import {PerspectiveCamera} from './../../node_modules/three/src/cameras/PerspectiveCamera';
import {CubeCamera} from './../../node_modules/three/src/cameras/CubeCamera';
import {Scene} from './../../node_modules/three/src/scenes/Scene';
import {Fog} from './../../node_modules/three/src/scenes/Fog';
import {CubeTextureLoader} from './../../node_modules/three/src/loaders/CubeTextureLoader';
import {MeshPhongMaterial} from './../../node_modules/three/src/materials/MeshPhongMaterial';
import {PlaneBufferGeometry} from './../../node_modules/three/src/geometries/PlaneGeometry';
import {SphereGeometry} from './../../node_modules/three/src/geometries/SphereGeometry';
import {BoxGeometry} from './../../node_modules/three/src/geometries/BoxGeometry';
import {TorusGeometry} from './../../node_modules/three/src/geometries/TorusGeometry';
import { SpotLight } from './../../node_modules/three/src/lights/SpotLight';
import { PointLight } from './../../node_modules/three/src/lights/PointLight';
import { DirectionalLight } from './../../node_modules/three/src/lights/DirectionalLight';
import { AmbientLight } from './../../node_modules/three/src/lights/AmbientLight';
import {ColladaLoader} from './ColladaLoader2';
import { LightShadow } from './../../node_modules/three/src/lights/LightShadow';
import { CameraHelper } from './../../node_modules/three/src/helpers/CameraHelper';
import { WebGLRenderer } from './../../node_modules/three/src/renderers/WebGLRenderer';
import { OrbitControls } from './OrbitControls';
import { TextureLoader } from './../../node_modules/three/src/loaders/TextureLoader';
import { Vector3 } from './../../node_modules/three/src/math/Vector3';
//import {AxisHelper} from './../../node_modules/three/src/Three';



window["ObjectViewer"] = function ObjectViewer(id, objpath)
{
	
	
	var bbMin;
	var bbMax;
	var center = new Vector3();
	var radius = 100;
	
	var v1 = new Vector3();
	var v2 = new Vector3();
	function UpdateBBox(o)
	{
		o.geometry.computeBoundingBox();
		var bb = o.geometry.boundingBox;
		v1.copy(bb.min).applyMatrix4(o.matrixWorld);
		v2.copy(bb.max).applyMatrix4(o.matrixWorld);
		
		if(bb === undefined) return;
		if(bbMin === undefined) bbMin = v1.clone();
		if(bbMax === undefined) bbMax = v2.clone();
		
		bbMin.x = Math.min(bbMin.x, v1.x);
		bbMin.y = Math.min(bbMin.y, v1.y);
		bbMin.z = Math.min(bbMin.z, v1.z);
		
		bbMax.x = Math.max(bbMax.x, v2.x);
		bbMax.y = Math.max(bbMax.y, v2.y);
		bbMax.z = Math.max(bbMax.z, v2.z);
		
		bbMin.x = Math.min(bbMin.x, bbMax.x);
		bbMin.y = Math.min(bbMin.y, bbMax.y);
		bbMin.z = Math.min(bbMin.z, bbMax.z);
		
		bbMax.x = Math.max(bbMax.x, bbMin.x);
		bbMax.y = Math.max(bbMax.y, bbMin.y);
		bbMax.z = Math.max(bbMax.z, bbMin.z);
		
		center.x = bbMin.x + (bbMax.x - bbMin.x)/2;
		center.y = bbMin.y + (bbMax.y - bbMin.y)/2;
		center.z = bbMin.z + (bbMax.z - bbMin.z)/2;	
	}
	function CalcMinDistance()
	{
		radius = v1.copy(center).sub(bbMin).length();
	}
	
	
	function CalculateBoundingBox(obj3d)
	{
		if(obj3d.geometry !== undefined) UpdateBBox(obj3d);
		for(var i = 0; i < obj3d.children.length ; i++)
		{
			CalculateBoundingBox( obj3d.children[i]);
		}
	}

	var canvas = document.getElementById(id);
	var controls;
	var camera, scene, renderer;

	camera = new PerspectiveCamera( 45, canvas.clientWidth / canvas.clientHeight, 2, 4000 );
	//camera.position.set( 30, 30, 200 );
	camera.position.set(0,0,radius);
	
	window["testf"] = function()
	{
		console.log(camera.position, controls);
		console.log(bbMin, bbMax);
	}
	scene = new Scene();
	//scene.fog = new Fog( 0, 1000, 10000 );
	
	

		// LIGHTS
		
		scene.add( new AmbientLight( 0xffffff,0.9));
		
	
		// RENDERER
		renderer = new WebGLRenderer( { antialias: true,canvas:canvas } );
		renderer.setClearColor(0xffffff);
		renderer.setPixelRatio( window.devicePixelRatio );
		renderer.setSize( canvas.clientWidth, canvas.clientHeight );
		renderer.shadowMap.enabled = true;
		renderer.shadowMap.type = THREE.PCFSoftShadowMap;
		renderer.gammaInput = true;
		renderer.gammaOutput = true;

		controls = new OrbitControls( camera, renderer.domElement );
		controls.target.set( 0, 0, 0 );
		controls.rotateSpeed = 0.3;
		controls.zoomSpeed = 1.2;
		controls.panSpeed = 0.8;
		controls.enableZoom = true;
		controls.enablePan = false;
		controls.enableDamping = true;
		controls.dampingFactor = 0.15;
		controls.keys = [ 65, 83, 68 ];
		
		var loader = new ColladaLoader();
		loader.options.convertUpAxis = true;
		loader.load( objpath, function ( collada ) {
			var object = collada.scene;
			object.scale.set( 1, 1, 1 );
			scene.add( object );
			object.updateMatrix();
			object.updateMatrixWorld();
			object.position.set(0,0,0);
			CalculateBoundingBox(object);
			CalcMinDistance();
			camera.position.copy(center).add(new Vector3(0,0,radius*2));
			controls.minDistance = radius;
			controls.maxDistance = radius * 5;
			controls.target.copy(center);
			
			/*
			var axis = new AxisHelper(100);
			axis.position.copy(center);
						
			var mi = new AxisHelper(300);
			mi.position.copy(bbMin);
			
			var ma = new AxisHelper(300);
			ma.position.copy(bbMax);
			
			scene.add(axis);
			scene.add(mi);
			scene.add(ma);
			*/
			
		});

		// EVENTS
		window.addEventListener( 'resize', onWindowResize, false );
	
	function onWindowResize( event ) {
		camera.aspect = canvas.clientWidth / canvas.clientHeight;
		camera.updateProjectionMatrix();
		renderer.setSize( canvas.clientWidth, canvas.clientHeight );
		
	}
			//
			function animate() {
				requestAnimationFrame( animate );
				render();
			}
			function render() {
				// update
				//var delta = clock.getDelta();
				controls.update();

				renderer.render( scene, camera );
			}
			//setInterval(render,1/35);
			animate();
}
export {ObjectViewer};